import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom'
import './index.scss';
import loadable from '@loadable/component';
import { Menu } from 'element-react';
import { fromJS, is } from 'immutable';

class App extends Component {
	state = {
		defaultActive: '',
		sidebarList: [
			{ title: 'swiper 插件', url: '/swiper' },
			{ title: 'echarts 图表', url: '/echarts' }
		]
	}

	componentDidMount() {
		this.resetActivePath(this.props.location.pathname)
		this.props.history.listen((route) => {
			this.resetActivePath(route.pathname)
		})
	}

	componentWillReceiveProps(nextProps) {
	}	

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	componentDidUpdate() {

	}

	componentWillUnmount() {
		this.setState = (state, callback) => {
			return;
		};
	}

	resetActivePath = async (pathname) => {
		var pathname = pathname || this.props.location.pathname
		let list = this.state.sidebarList
		for (var i = 0, len = list.length; i < len; i++) {
			if (pathname.endsWith(list[i].url)) {
				await this.setState({
					defaultActive: i
				})
				return;
			}
		}
		await this.setState({
			defaultActive: 0
		})
	}

	onSelect = async (index) => {
		await this.setState({
			defaultActive: index
		})
		if (index !== '0') {
			this.props.history.push('/demo' + this.state.sidebarList[index].url);
		} else {
			this.props.history.push('/demo');
		}
	}

	thisComponent = () =>{
		var pathname = this.props.location.pathname
		if (pathname == '/demo') return loadable(() => import('../swiper'))
		let path = pathname.split('/')[2]
		let list = this.state.sidebarList
		for (var i = 0, len = list.length; i < len; i++) {
			if (pathname == '/demo' + list[i].url) {
				return loadable(() => import('../' + path))
			}
		}
		return loadable(() => import('../swiper'))
	}

	render() {
		let { defaultActive, sidebarList } = this.state;
		return (
			<div id="component" className="flex">
				<Menu mode="vertical" className="sidebar pb-20" onSelect={this.onSelect}>
					{
						sidebarList.map((val, idx) => {
							return <Menu.Item index={idx.toString()} key={idx.toString()} 
								className={defaultActive == idx ? 'active' : null}>{val.title}</Menu.Item>
						})
					}
				</Menu>

				<div className="right">
					<Switch>
						<Route path={this.props.location.pathname} exact component={this.thisComponent()} />
					</Switch>
				</div>
			</div>
		);
	}
}

export default App;
